<template>
  <div id="JobChance">
    <div class="banner container-fuild text-center">工作机会</div>
    <div class="container">
      <div class="JobChance-container wow pulse">
        <h2>广告招商经理</h2>
        <p>岗位职责</p>
        <ol>
          <li>线上线下发掘、引进客户资源，对接、洽谈、合作；</li>
          <li>进入市场走访，收集商户信息，建立数据库；</li>
          <li>签订销售合同，达成招商工作指标；</li>
          <li>跟进商户进驻，维护长期合作关系。</li>
        </ol>
        <p>任职要求</p>
        <ol>
          <li>大专以上学历，优秀者可放宽要求；</li>
          <li>善于沟通，胜任商务谈判、资源整合；</li>
          <li>形象大方得体，具有客户服务意识；</li>
          <li>曾从事电视台、广播、广告展会等行业客户开发和招商工作；</li>
          <li>拥有一定产业资源，在郑州区域有客户资源者优先</li>
        </ol>
        <button class="center-block btn btn-warning btn-lg">投递简历</button>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from 'wowjs';
export default {
  name: "JobChance",
  data() {
    return {};
  },
  mounted(){
    var wow = new WOW();
    wow.init();
  }
};
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner_2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
.JobChance-container {
  margin: 100px;
  padding: 30px;
  transition: all ease 0.5s;
  border: 1px dashed salmon;
}
.JobChance-container h2{
  color: rgb(255, 102, 19);
  font-weight: bolder;
  text-align: center;
}
.JobChance-container p{
font-size: 20px;
  color: rgb(255, 102, 19);
  font-weight: 700;
}
.JobChance-container ol{
    list-style-type: decimal;
    padding-left: 30px;
}
.JobChance-container ol>li{
    font-size: 14px;
    line-height: 2.7rem;
}
@media screen and (max-width: 997px) {
  .JobChance-container {
    margin: 20px 0;
    padding: 20px;
    border: 1px dashed salmon;
  }
}
</style>




